.about-people {
	display: grid;
	grid-template-columns: 1fr 1fr;
	justify-items: center;
	justify-content: center;
	grid-gap: 30px;

	.wide {
		grid-column: 1/3;
	}

	img {
		width: 200px;
		display: block;
		margin: 20px auto;
		position: relative !important;
	}
}

@media only screen and (max-width: 600px) {
	.about-people {

		.left, .right {
			grid-column: 1/3;
		}
	}
}